import React, { useEffect, useState } from 'react';
import { PieChart, Pie, Cell, Tooltip, ResponsiveContainer, Legend } from 'recharts';
import Papa from 'papaparse';

const COLORS = ['#8884d8', '#82ca9d', '#ffc658'];

export default function FundPortChart() {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + '/data/fund_port_predicted.csv')
      .then((res) => res.text())
      .then((csv) => {
        Papa.parse(csv, {
          header: true,
          dynamicTyping: true,
          complete: (result) => {
            const rows = result.data;
            const latest = rows.find((row) => Number(row.year) === 2060);

            if (latest) {
              const transformed = [
                { category: '', ratio: latest.welfare },
                { category: '', ratio: latest.finance },
                { category: '', ratio: latest.others }
              ];
              setData(transformed);
            }
          },
        });
      });
  }, []);

  return (
    <ResponsiveContainer width="100%" height={400}>
      <PieChart>
        <Pie
          data={data}
          dataKey="ratio"
          nameKey="category"
          cx="50%"
          cy="50%"
          outerRadius={100}
          label
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
        <Tooltip />
        <Legend />
      </PieChart>
    </ResponsiveContainer>
  );
}
